import Vector from "../geom/Vector";
/**
 * User: kgoulding
 * Date: 3/16/2016
 * Time: 2:31 PM
 */
class SlopeAnalysis {

    constructor() {
    }

    _analyzeCell(table, reach, x, y) {
        var ht = table[x][y];
        var vectors = [];
        for (let x1 = Math.max(0, x - reach); x1 <= Math.min(table.length - 1, x + reach); x1++) {
            for (let y1 = Math.max(0, y - reach); y1 <= Math.min(table[x].length - 1, y + reach); y1++) {
                if (x1 === x && y1 === y) continue;
                var ht1 = table[x1][y1];
                vectors.push(new Vector(x1 - x, y1 - y, ht1 - ht));
            }
        }
        return this._addVectors(vectors);
    }

    _addVectors(vectors) {
        var v = new Vector(0, 0, 0);
        vectors.forEach(function (vector, i) {
            v = v.add(vector);
        });
        return v.unit();
    }

    _dist(x, y, x1, y1) {
        return Math.sqrt(Math.pow(x - x1, 2) + Math.pow(y - y1, 2))
    }

    /**
     * Convert a string containing two comma-separated numbers into a point.
     * @param {Array} table - 2D array [x][y] containing the height values
     * @param {Number} reach - a number specifying how far to look (level of smoothing)
     * @return {Array} 2D array [x][y] containing the slope values for each cell
     */
    analyzeData(table, reach) {
        var ans = [];
        for (var x = 0; x < table.length; x++) {
            var col = table[x];
            ans[x] = [];
            for (var y = 0; y < col.length; y++) {
                ans[x][y] = this._analyzeCell(table, reach, x, y);
            }
        }
        return ans;
    }
}

export default SlopeAnalysis;


