import SlopeAnalysis from "./slope/SlopeAnalysis";
var slopeAnalysis = new SlopeAnalysis();

var myImg = new Image();
myImg.onload = function () {
    var contextIn = document.getElementById('canvasIn').getContext('2d');
    var contextOut = document.getElementById('canvasOut').getContext('2d');
    contextIn.drawImage(myImg, 0, 0);
    var imageData = contextIn.getImageData(0, 0, myImg.width, myImg.height);
    var data = imageData.data;
    var dataTable = [];
    for (let i = 0; i < data.length; i += 4) {
        let x = (i / 4) % imageData.width;
        let y = Math.floor((i / 4) / imageData.width);
        let ht = data[i];
        if (!dataTable[x]) {
            dataTable[x] = [];
        }
        dataTable[x][y] = ht;
    }

    var table = [];
    var vectorTable = slopeAnalysis.analyzeData(dataTable, 2);
    vectorTable.forEach(function (col, x) {
        table[x] = [];
        col.forEach(function (v, y) {
            table[x][y] = v.toAngles().theta * 180 / Math.PI;
        });
    });

    var outData = contextOut.getImageData(0, 0, myImg.width, myImg.height);

    for (let i = 0; i < outData.data.length; i += 4) {
        let x = (i / 4) % outData.width;
        let y = Math.floor((i / 4) / outData.width);
        var gray = Math.round(128 + table[x][y] * 2);
        if (gray !== 128) {
            // console.log(x, y, gray);
        }
        gray = Math.max(0, Math.min(255, gray));
        outData.data[i] = gray;
        outData.data[i + 1] = 0;
        outData.data[i + 2] = gray;
        outData.data[i + 3] = 255;
    }
    contextOut.putImageData(outData, 0, 0);
};
myImg.src = 'topo.png';

